@import 'https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Montserrat:wght@400;700&display=swap';

*, *::before, *::after {
    margin: 0; 
    padding: 0; 
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

html {
    font-family: 'Montserrat', sans-serif;
    /* width: 100vw; */
    overflow-y: scroll;
}

h1, h2 {
    font-family: 'Inter', sans-serif;
}

li>a {
    color: inherit;
}


.grid {
    display: grid; 
    min-height: 100vh; 
    max-height: 100vh;
    max-width: 100vw;
    grid-template-rows: auto 1fr auto;
}

.flex {
    display: flex; 
    flex-direction: column;
    flex-wrap: wrap;
}


.inherit-color {
    color: inherit;
}

.hidden {
    display: none !important;
}